.NotFound {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    background-color: #fff;
    top: 0;
    z-index: 12;

    h1 {
        font-size: 190px;
    }

    p {
        font-size: 30px;
    }
}